/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */

import { Fragment } from '@wordpress/element';
import { InspectorControls, PanelColorSettings } from '@wordpress/block-editor';
import { RangeControl, TextControl, PanelBody, SelectControl } from '@wordpress/components';
import { useBlockProps } from '@wordpress/block-editor';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */

import ServerSideRender from '@wordpress/server-side-render';

export default function Edit( props ) {
	const { attributes, setAttributes } = props;
	const { blockId, endDate, daysLabel, hoursLabel, minutesLabel, secondsLabel } = attributes;

	const strRandom = () => {
		var result = '';
		const characters = 'abcdefghijklmnopqrstuvwxyz0123456789';
		var length = 10;
		
		// Loop to generate characters for the specified length
		for (let i = 0; i < length; i++) {
			const randomInd = Math.floor(Math.random() * characters.length);
			result += characters.charAt(randomInd);
		}
		return result;
	}

	if (!blockId) {
        setAttributes({ blockId: strRandom() });
    }

	return (
		<div { ...useBlockProps() }>
			<Fragment>
				<InspectorControls>
					<PanelBody title={ __( 'CountDown Settings', 'wp-travel-blocks' ) } >
						<TextControl
							label={"End Date/Time"}
							value={endDate}
							onChange={ value => setAttributes(
								{
									endDate: value
								}
							) }
						/>
						<TextControl
							label={"Days Label"}
							value={daysLabel}
							onChange={ value => setAttributes(
								{
									daysLabel: value
								}
							) }
						/>
						<TextControl
							label={"Hours Label"}
							value={hoursLabel}
							onChange={ value => setAttributes(
								{
									hoursLabel: value
								}
							) }
						/>
						<TextControl
							label={"Minutes Label"}
							value={minutesLabel}
							onChange={ value => setAttributes(
								{
									minutesLabel: value
								}
							) }
						/>
						<TextControl
							label={"Seconds Label"}
							value={secondsLabel}
							onChange={ value => setAttributes(
								{
									secondsLabel: value
								}
							) }
						/>
					</PanelBody>
				</InspectorControls>
			</Fragment>
			<ServerSideRender
				block="wp-travel-blocks/countdown"
				attributes={ props.attributes }
			/>
		</div>
	);
}
